*{
    box-sizing: border-box;
}
.Navbar_main{
    width: 100vw;
    height: 58px;
    position: fixed;
    background: rgb(131, 69, 189);
    display: flex;
    justify-content: space-between;
    border-radius: 1px;
    z-index: 100;
}
.Navbar_logo{
    width: 80px !important;
   height: 55px !important;
   transition: 0.8s ease;
   margin-left: 15px;
  
}
.Navbar_logo img{
    width: 100% ;
    height: 100%;
}
.Navbar_items{
    padding-right: 8px;
    display: flex;
   
   
}
#id{
    width:40px;
    height: 45px;
    margin-right: 25px;
    display: none;

}
.active{
    border:groove red;
    box-shadow: 1px 1px 4px yellow;
}
.bar{
    width:25px !important;
    height: 30px !important;
    color:black;

}
.Navbar_links{
    text-decoration: none;
    color:white;
    font-size: 16px;
    padding: 0 10px;
    font-weight: bolder;
    transition: 0.8s ease;
    align-content: center;
    align-self: center;
}


.Navbar_cart{
    height: 32px !important;
    width: 28px !important;
    margin-right: 35px !important;
    padding-top: 5px !important;

}
.close_icon{
    display:none;
}
.Navbar_links:hover,.active{
    color: rgb(238, 184, 21);
    border-bottom: 3px solid red;
    border-style: groove;
    margin:0px 12px
    

}
.Navbar_logo:hover{
    cursor: pointer;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
@media(max-width:768px){
    .Navbar_items{
        display: flex;
        align-items: flex-start;
