*{
  margin: 0; 
}


.main_Nav{
    top:0;
    left: 0;
    position: fixed;
    z-index: 10000;
}
.table_main{
    margin-top: 100px;
    width: 100%;
    height: 100%;
    color: black;
    border: 2px solid black;
    overflow-x: auto;
}
.table_main table{
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;
    white-space:nowrap;
}
.table_main table thead{
    border:1px solid rgb(135, 131, 131);
    border-left: none;
    border-right: none;
}
.table_main table thead td{
    font-weight:700;
    text-transform: uppercase;
    font-size: 13px;
    padding: 18px 0;
}
.table_main table tbody tr td{
    padding-top: 35px;
}

.table_main table tbody  td{
    font-size: 13px;
}

.table_main img{
    width: 70px;
    height: 50px;
}
.table_main table td:nth-child(1){
    width: 100px;
    text-align: center;

}
.table_main table td:nth-child(2){
    width: 150px;
    text-align: center;

}
.table_main table td:nth-child(3){
    width: 250px;
    text-align: center;

}
.table_main table td:nth-child(4),.table_main table td:nth-child(5),.table_main table td:nth-child(6){
    width: 150px;
    text-align: center;

}
.table_main table td:nth-child(5) input{
    width: 70px;
    padding: 10px 5px 10px 15px;
}
.Cart_add{
    display: flex;
    flex-wrap: wrap;
    margin-top: 25px;
    width: 100%;
    justify-content: space-between;
   
    border: 1px solid black;
}
 .coupon{
 width: 50%;
 margin-bottom: 30px;
 display: flex;
 flex-direction: column;
}
.coupon h3,#subtotal h3{
    padding-bottom: 15px;
}
.coupon input{
    padding: 10px 20px;
    outline: none;
    width: 60%;
    margin-right: 10px;
    border: 1px solid rgb(131, 128, 128);
}
.coupon button ,.normal{
    background-color: rgb(21, 87, 21);
    color: rgb(244, 239, 239);
    padding: 12px 20px;
    border-radius: 5px ;
    cursor: pointer;
}
#subtotal{
    width: 50%;
    margin-bottom: 30px;
    border: 1px solid rgb(120, 110, 110);
    padding: 30px;
}
#subtotal table{
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 20px;
}
#subtotal table td{
    width: 50%;
